  display: flex;
  height: 100%;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  margin: 0;
  position: fixed;
  width: 100%;
  z-index: 99999;
}

.prelolader .filter {
  position: absolute;
  visibility: hidden;
}

.prelolader .dots {
  -webkit-filter: url(#gooeyness);
  filter: url(#gooeyness);
  padding: 30px;
}

.prelolader .dots .dot {
  background: #FF6863;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  display: inline-block;
  margin-right: 20px;
  width: 32px;
  height: 32px;
}

.prelolader .dots .dot:first-child {
  -webkit-animation: FirstDot 3s infinite;
  animation: FirstDot 3s infinite;
}

.prelolader .dots .dot:nth-child(2) {
  -webkit-animation: SecondDot 3s infinite;
  animation: SecondDot 3s infinite;
}

.prelolader .dots .dot:nth-child(3) {
  -webkit-animation: ThirdDot 3s infinite;
  animation: ThirdDot 3s infinite;
}

.prelolader .dots .dot:nth-child(4) {
  -webkit-animation: FourthDot 3s infinite;
  animation: FourthDot 3s infinite;
}

.prelolader .dots .dot:nth-child(5) {
  -webkit-animation: FifthDot 3s infinite;
  animation: FifthDot 3s infinite;
}

@-webkit-keyframes FirstDot {
  0% {
    -webkit-transform: scale(1) translateX(0);
    transform: scale(1) translateX(0);
  }

  25% {
    -webkit-transform: scale(2.5) translateX(0);
    transform: scale(2.5) translateX(0);
  }

  50% {
    -webkit-transform: scale(1) translateX(0);
    transform: scale(1) translateX(0);
  }

  83% {
    -webkit-transform: scale(1) translateX(240px);
    transform: scale(1) translateX(240px);
  }

  100% {
    -webkit-transform: scale(1) translateX(0);
    transform: scale(1) translateX(0);
  }
}

@keyframes FirstDot {
  0% {
    -webkit-transform: scale(1) translateX(0);
