body {
  min-width: 320px;
  font-family: 'Circe', Arial, sans-serif;
}
* {
  line-height: 1.55;
  margin: 0;
  padding: 0;
}
*:focus,
*:active {
  outline: none;
  border: none;
}
input[type='number'] {
  -moz-appearance: textfield;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.wrap {
  max-width: 1200px;
  margin: auto;
  padding: 0 15px;
}
.row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.line {
  margin: 20px auto;
  width: 100px;
  height: 3px;
  background: #FF6863;
}
.title h4 {
  text-align: center;
  font-size: 42px;
  font-weight: 600;
  text-transform: uppercase;
  color: #444444;
}
.prelolader {
  align-items: center;
  background: #fff;
  display: flex;
  height: 100%;
  justify-content: center;
  margin: 0;
  position: fixed;
  width: 100%;
  z-index: 99999;
}
.prelolader .filter {
  position: absolute;
  visibility: hidden;
}
.prelolader .dots {
  filter: url(#gooeyness);
  padding: 30px;
}
.prelolader .dots .dot {
  background: #FF6863;
  border-radius: 50%;
  display: inline-block;
  margin-right: 20px;
  width: 32px;
  height: 32px;
}
.prelolader .dots .dot:first-child {
  animation: FirstDot 3s infinite;
}
.prelolader .dots .dot:nth-child(2) {
  animation: SecondDot 3s infinite;
}
.prelolader .dots .dot:nth-child(3) {
  animation: ThirdDot 3s infinite;
}
.prelolader .dots .dot:nth-child(4) {
  animation: FourthDot 3s infinite;
}
.prelolader .dots .dot:nth-child(5) {
  animation: FifthDot 3s infinite;
}
@keyframes FirstDot {
  0% {
    transform: scale(1) translateX(0);
  }
  25% {
    transform: scale(2.5) translateX(0);
  }
  50% {
    transform: scale(1) translateX(0);
  }
  83% {
    transform: scale(1) translateX(240px);
  }
  100% {
    transform: scale(1) translateX(0);
  }
}
@keyframes SecondDot {
  0% {
    transform: translateX(0px);
  }
  27% {
    transform: translateX(-40px);
  }
  50% {
    transform: translateX(0px);
  }
  81% {
    transform: translateX(180px);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes ThirdDot {
  0% {
    transform: translateX(0px);
  }
  29% {
    transform: translateX(-100px);
  }
  50% {
    transform: translateX(0px);
  }
  79% {
    transform: translateX(120px);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes FourthDot {
  0% {
    transform: translateX(0px);
  }
  31% {
    transform: translateX(-160px);
  }
  50% {
    transform: translateX(0px);
  }
  77% {
    transform: translateX(60px);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes FifthDot {
  0% {
    transform: scale(1) translateX(0);
  }
  33% {
    transform: scale(1) translateX(-220px);
  }
  50% {
    transform: scale(1) translateX(0);
  }
  75% {
    transform: scale(2.5) translateX(0);
  }
  100% {
    transform: scale(1) translateX(0);
  }
}
#top_menu {
  width: 100%;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0);
  z-index: 999;
  padding: 10px 0;
  position: fixed;
  transition: all 0.25s ease-in-out;
  color: #444444;
}
#top_menu.active {
  background: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.5) 0px 1px 3px;
}
#top_menu.active .wrap .row .logo a {
  color: #444444;
}
#top_menu.active .wrap .row .elements_top_menu nav ul li a {
  color: #444444;
}
#top_menu.active .wrap .row .elements_top_menu nav ul li:hover::after {
  opacity: 1;
  background: #444444;
}
#top_menu.active .wrap .row .elements_top_menu nav ul li:hover a {
  color: #444444;
}
#top_menu.active .wrap .row .elements_top_menu .social_network .number {
  color: #444444;
}
#top_menu .wrap .row .logo a {
  text-decoration: none;
  justify-content: flex-start;
  color: #fff;
}
#top_menu .wrap .row .logo a img {
  margin-right: 10px;
  width: 35px;
}
#top_menu .wrap .row .logo a .description_logo h1 {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  width: auto;
}
#top_menu .wrap .row .elements_top_menu nav {
  margin-right: 25px;
}
#top_menu .wrap .row .elements_top_menu nav ul {
  display: flex;
  align-items: center;
  justify-content: center;
}
#top_menu .wrap .row .elements_top_menu nav ul li {
  list-style: none;
  margin: 0 20px;
  position: relative;
  transition: all 0.3s ease-in-out;
}
#top_menu .wrap .row .elements_top_menu nav ul li:first-child {
  margin-left: 0;
}
#top_menu .wrap .row .elements_top_menu nav ul li:last-child {
  margin-right: 0;
}
#top_menu .wrap .row .elements_top_menu nav ul li::after {
  content: '';
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  margin-bottom: -5px;
  height: 1px;
  opacity: 0;
  background: #fff;
  transition: all 0.3s ease-in-out;
}
#top_menu .wrap .row .elements_top_menu nav ul li:hover::after {
  opacity: 1;
}
#top_menu .wrap .row .elements_top_menu nav ul li:hover a {
  color: #fff;
}
#top_menu .wrap .row .elements_top_menu nav ul li a {
  font-size: 15px;
  transition: all 0.3s ease-in-out;
  text-transform: uppercase;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
}
#top_menu .wrap .row .elements_top_menu .social_network {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}
#top_menu .wrap .row .elements_top_menu .social_network .row {
  order: 2;
  justify-content: flex-end;
}
#top_menu .wrap .row .elements_top_menu .social_network .number {
  order: 1;
  font-size: 18px;
  font-weight: 400;
  color: #fdfbfb;
}
#top_menu .wrap .row .elements_top_menu .social_network a {
  width: 40px;
  display: block;
  margin: 0 5px;
}
#top_menu .wrap .row .elements_top_menu .social_network a:last-child {
  margin-right: 0;
}
#top_menu .wrap .row .elements_top_menu .social_network a:first-child {
  margin-left: 20px;
}
#top_menu .wrap .row .elements_top_menu .social_network a img {
  width: 100%;
  height: auto;
}
#top_menu .wrap .row .elements_top_menu .social_network .lang {
  order: 3;
  margin-left: 20px;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: flex-end;
}
#top_menu .wrap .row .elements_top_menu .social_network .lang > img {
  cursor: pointer;
  height: 50px;
}
#top_menu .wrap .row .elements_top_menu .social_network .lang .drop {
  border-radius: 5px;
  background: #ffffff;
  padding: 0 15px;
  position: absolute;
  display: none;
  bottom: 0;
  transform: translateY(100%);
  border: 1px solid rgba(0, 0, 0, 0.5);
  /* Добавляем треугольник */
}
#top_menu .wrap .row .elements_top_menu .social_network .lang .drop::after {
  content: '';
  position: absolute;
  /* Абсолютное позиционирование */
  right: 15px;
  top: -20px;
  /* Положение треугольника */
  border: 10px solid transparent;
  /* Прозрачные границы */
  border-bottom: 10px solid rgba(0, 0, 0, 0.5) /* Добавляем треугольник */;
}
#top_menu .wrap .row .elements_top_menu .social_network .lang .drop::before {
  content: '';
  position: absolute;
  /* Абсолютное позиционирование */
  right: 15px;
  z-index: 5;
  top: -19px;
  /* Положение треугольника */
  border: 10px solid transparent;
  /* Прозрачные границы */
  border-bottom: 10px solid #ffffff;
  /* Добавляем треугольник */
}
#top_menu .wrap .row .elements_top_menu .social_network .lang .drop a {
  color: #222;
  font-size: 18px;
  text-decoration: none;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px #444444 solid;
  padding: 5px 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 85%;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
#top_menu .wrap .row .elements_top_menu .social_network .lang .drop a:hover,
#top_menu .wrap .row .elements_top_menu .social_network .lang .drop a.active {
  background: #444444;
  color: #fff;
}
#top_menu .wrap .row .elements_top_menu .social_network .lang .drop a img {
  width: auto;
  margin-right: 20px;
  height: 45px;
}
#top_menu .wrap .row .toggle_btn {
  display: none;
}
header {
  padding-top: 80px;
  min-height: 700px;
  background: url(../img/header.jpg) 50% 50% / cover no-repeat;
  position: relative;
  z-index: 0;
}
header::after {
  z-index: -1;
  content: '';
  width: 100%;
  height: 200px;
  bottom: -2px;
  left: 0;
  position: absolute;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
}
header::before {
  z-index: -1;
  content: '';
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  background: rgba(0, 0, 0, 0.4);
}
header .wrap {
  text-align: left;
}
header .wrap h2 {
  padding: 50px 0 0;
  color: #fff;
  font-weight: 600;
  text-transform: capitalize;
  font-size: 50px;
}
header .wrap .row {
  margin-top: 70px;
  justify-content: flex-start;
}
header .wrap .row button {
  border: none;
  padding: 18px 33px;
  font-size: 16px;
  margin: 0 10px;
  cursor: pointer;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 600;
  transition: all 0.3s ease-in-out;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 10px 0px;
}
header .wrap .row button:hover {
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.3);
}
header .wrap .row button:last-child {
  margin-right: 0;
}
header .wrap .row button:first-child {
  margin-left: 0;
}
header .wrap .row .consultation {
  background: #FF6863;
  color: #fff;
  border: 2px #FF6863 solid;
}
header .wrap .row .consultation:hover {
  background: #fff;
  border-color: #FF6863;
  color: #FF6863;
}
header .wrap .row .detailed {
  background: none;
  color: #fff;
  border: 2px #fff solid;
}
header .wrap .row .detailed:hover {
  border-color: #FF6863;
  background: #FF6863;
  color: #fff;
}
header .wrap .don_btn {
  margin: 150px auto 0;
  width: 50px;
  cursor: pointer;
  animation: movement 1.7s infinite ease;
}
header .wrap .don_btn img {
  width: 100%;
  height: auto;
}
