/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px)
{
    #top_menu{
        .wrap{
            .row{
                .elements_top_menu {
                    .social_network {
                        .row{
                            display: none;
                        }
                    }
                }
            }
        }
    }

    header {
        .wrap{ 
            h2{
                font-size: 40px;
            }
        }
    }

    .video_blog{
        .wrap{
            .row{
                align-items: flex-start;
                .text{
                    width: 48%;
                    padding: 10px;
                    h3{
                        font-size: 28px;
                    }
                    p{
                        font-size: 16px;
                    }
                }
                .video{
                    width: 45%;
                }
            }
        }
    }

    .news{
        .wrap{
            .primary_news{
                .image{
                    height: 300px;
                }
            }
        }
    }

    .device{
        .wrap{
            .row{
                .image{
                    width: 40%;
                }
                .text{
                    width: 50%;
                    p{
                        font-size: 16px;
                    }
                }  
            }  
        }
    }

}


/* Medium Devices, Desktops */
@media only screen and (max-width : 992px)
{

    #top_menu{
        .wrap{
            .row{
                .logo {
                    a{ 
                        .description_logo{
                            h1{
                                font-size: 14px;
                            }
                        }
                    }
                }
                .elements_top_menu {
                    .social_network {
                        .number{
                            display: none;
                        }
                    }
                }
            }
        }
    }

    .video_blog{
        .wrap{
            .row{
                align-items: center;
                flex-direction: column-reverse;
                .text{
                    width: 85%;
                    padding: 5px;
                    text-align: center;
                    .line{
                        margin: 10px auto 20px;
                    }
                }
                .video{
                    width: 550px;
                    margin-bottom: 50px;
                }
            }
        }
    }

    .device{
        .wrap{
            .row{
                flex-direction: column;
                align-items: center;
                .image{
                    width: 450px;
                    margin: 50px auto;
                }
                .text{
                    width: 95%;
                    p{
                        font-size: 18px;
                    }
                }  
            } 
        }
    }

    .advantage{
        .wrap{
            .advantage_list{
                justify-content: center;
                .box{
                    width: 340px;
                    justify-content: space-between;
                    margin: 0 0px 60px;
                    .numb{
                        margin-right: 15px;
                    }
                }
            }
        }
    }

    .install{
        .wrap{
            .install_box{
                align-items: center;
                flex-direction: column;
                .image{
                    width: 50%;
                    p{
                        margin-top: 20px;
                        font-size: 22px;
                    }
                    margin-bottom: 50px;
                }
                .text{
                    width: 90%;
                    p{
                        font-size: 18px;
                    }
                }
            }
        }
    }

    .delete{
        .wrap{
            .delete_box{
                align-items: center;
                flex-direction: column;
                .image{
                    margin-top: 50px;
                    width: 60%;
                    p{
                        margin-top: 20px;
                        font-size: 22px;
                    }
                    margin-bottom: 50px;
                }
                .text{
                    width: 90%;
                    p{
                        font-size: 18px;
                    }
                }
            }
        }
    }

    .news {
        .wrap{
            .primary_news{
                flex-direction: column;
                align-items: center;
                .image{
                    margin: auto;
                    width: 70%;
                }
                .description_primal_news{
                    text-align: center;
                    margin-top: 25px;
                    width: 80%;
                    .line{
                        margin: 15px auto;
                    }
                }
            }
            .last_news{
                width: auto;
                flex-wrap: wrap;
                .box_news:nth-of-type(1){
                    flex-basis: 45%;
                    flex-grow: 0;
                }   
                .box_news:nth-of-type(2){
                    flex-basis: 45%;
                }   
                .box_news:nth-of-type(3){
                    flex-basis: 100%;
                    width: 450px;
                    margin: 40px 0 0;
                    .image{
                        height: 300px;
                    }
                    .description_last_news{
                        margin-top: 20px;
                    }
                }   
            }
        }
    }

    .all_news{
        margin: 120px 0 50px;
        .wrap{
            >.row{
                .description{
                    padding: 0 5px 0 15px;
                    .text{
                        margin-top: 10px;
                    }
                    .link{
                        margin-top: 10px;
                    }
                }
            }
        }
    }

    .contact_us {
        .wrap{
            >.row{
                flex-direction: column;
                .title{
                    margin-bottom: 20px;
                    width: 100%;
                    h4{
                        text-align: center;
                    }
                    .line{
                        margin: 20px auto;
                    }
                    .description{
                        text-align: center;
                    }
                }
                .soc_icons_contact{
                    margin-left: 0;
                    width: 100%;
                    display: flex;
                }
            }
        }
    }
}


/* Small Devices, Tablets */
@media only screen and (max-width : 768px)
{

    .prelolader{
        .dots {
            padding: 30px;
            .dot {
                width: 28px;
                height: 28px;
            }                  
        }
    }

    #top_menu{
        &.active{
            .wrap{
                .row{
                    .toggle_btn{
                        span{
                            background: #000;
                        }
                    }
                    .elements_top_menu{
                        nav{
                            ul{
                                li{
                                    a{
                                        color: #fff;
                                    }
                                    &:hover{
                                        a{
                                            color: #fff;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .wrap{
            .row{
                .elements_top_menu{
                    width: 100%;
                    display: none;
                    top: 0;
                    z-index: 998;
                    left: 0;
                    background: rgba(0, 0, 0, 0.8);
                    flex-direction: column;
                    justify-content: flex-start;
                    position: fixed;
                    min-height: 100vh;
                    padding-top: 75px;
                    nav{
                        margin-right: 0;
                        ul{
                            flex-direction: column;
                            text-align: center;
                            align-items: center;
                            li{
                                margin: 15px 0;
                                a{
                                    color: #fff;
                                    font-size: 18px;
                                }
                                &:last-child{
                                    margin-bottom: 0;
                                }
                            }
                        }
                    }
                    .social_network{
                        justify-content: center;
                            .lang{
                                margin-top: 30px;
                                margin-left: 0;
                                >img{
                                    display: none;
                                }
                                .drop{
                                    transform: translateY(0);
                                    display: flex;
                                    position: relative;
                                    &::after, &::before{
                                        display: none;
                                    }
                                    a{
                                        width: auto;
                                        margin: 10px;
                                    }
                                }
                            }
                    }
                }
                .toggle_btn{
                    z-index: 999;
                    position: absolute;
                    top: 50%;
                    margin-top: -25px;
                    right: 15px;
                    height: 50px;
                    width: 40px;
                    display: block;
                    cursor: pointer;
                    &.active{
                        span{
                            background: #fff;
                            &:nth-of-type(2){
                                opacity: 0;
                                height: 0;
                                transform: rotate(45deg);
                            }
                            &:nth-of-type(1){
                                margin-top: 0;
                                transform: rotate(45deg);
    
                            }
                            &:nth-of-type(3){
                                margin-top: 0;
                                transform: rotate(-45deg);
                            }
                        }
                    }
                    span{
                        width: 100%;
                        position: absolute;
                        height: 5px;
                        transform: translateY(-50%);
                        border-radius: 5px;
                        background: #fff;
                        top: 50%;
                        &:nth-of-type(1){
                            margin-top: -10px;
    
                        }
                        &:nth-of-type(3){
                            margin-top: 10px;
                        }
                    }
                }
            }
        }
    }

    header{
        min-height: 100vh;
        .wrap {
            h2{
                padding-top: 80px;
                font-size: 34px;
            }
            a{
                font-size: 25px;
            }
            .row{
                flex-direction: column;
                button{
                    width: 95%;
                    margin: 10px 0;
                    &:last-child{
                        margin-bottom: 0;
                    }
                    &:first-child{
                        margin-top: 0;
                    }
                }
            }
            .don_btn{
                margin-top: 20px;
            }
        }
    }

    .about{
        .wrap{
            .title{
                h4{
                    font-size: 35px;
                }
            }
            .line{
                margin-bottom: 30px;
            }
            .text{
                width: 95%;
                font-size: 22px;
            }
        }
    }

    .video_blog{
        .wrap{
            .row{
                .text{
                    width: 95%;
                    padding: 5px;
                    text-align: center;
                    h3{
                        font-size: 28px;
                    }
                }
                .video{
                    width: 450px;
                    margin-bottom: 50px;
                }
            }
        }
    }

    .device{
        .wrap{
            .title{
                h4{
                    font-size: 35px;
                }
            } 
        }
    }

    .advantage{
        .wrap{
            .title{
                h4{
                    font-size: 35px;
                }
            }
            .advantage_list{
                flex-direction: column;
                .box{
                    flex-direction: column;
                    width: 100%;
                    align-items: center;
                    margin: 0 0px 60px;
                    .numb{
                        margin: 0 auto 15px ;
                    }
                    .text{
                        text-align: center;
                        width: 90%;
                        h4{
                            margin: 20px auto;
                        }
                    }
                }
            }
        }
    }

    .danger{
        .wrap{
            .title{
                h4{
                    font-size: 35px;
                }
            }
            .text{
                ul{
                    li{
                        margin-left: 20px;
                        font-size: 22px;
                    }
                }
            }
        }
    }

    .after{
        .wrap{
            .title{
                h4{
                    font-size: 35px;
                }
            }
            .image{
                flex-direction: column;
                .box{
                    margin: 15px 0;
                    width: 80%;
                }
            }
        }
    }

    .install{
        .wrap{
            .title{
                h4{
                    font-size: 35px;
                }
            }
            .install_box{
                .image{
                    p{
                        text-align: center;
                        font-size: 18px;
                    }
                }
            }
        }
    }

    .delete{
         .wrap{
            .title{
                h4{
                    font-size: 35px;
                }
            }
        }
    }

    .question{
        .wrap{
            .title{
                h4{
                    font-size: 35px;
                }
            }
            .question_list{
                .question_box{
                    .detailed{
                        .question_mark{
                            height: auto;
                        }
                        .title_detailed{
                            height: auto;
                            font-size: 22px;
                        }
                        .down{
                            height: auto;
                        }
                    }
                }
            }
        }
    }

    .news {
        .wrap{
            .title{
                h4{
                    font-size: 35px;
                }
            }
            .primary_news{
                .image{
                    width: 90%;
                }
                .description_primal_news{
                    margin-top: 25px;
                    width: 95%;
                    .text{
                        text-align: justify;
                    }
                }
            }
            .last_news{
                .box_news{
                    margin: 0 0 50px;
                    .image{
                        height: 280px;
                    }
                    .description_last_news {
                        .title{
                            font-size: 28px;
                        }
                        .text{
                            font-size: 20px;
                        }
                        .link{
                            font-size: 18px;
                        }
                    }
                    &:nth-of-type(3){
                        margin-top: 0;
                        .description_last_news{
                            margin-top: 0;
                        }
                        .image{
                            height: 280px;
                        }
                    }
                    &:nth-of-type(1),
                    &:nth-of-type(2),
                    &:nth-of-type(3){
                        flex-basis: 100%;
                    }
                }  
            }
        }
    }

    .about_us{
        .wrap{
            .title{
                h4{
                    font-size: 35px;
                }
            }
            .about{
                .title_about{
                    p{
                        font-size: 25px;
                    }
                }
            }
        }
    }

    .contact_us{
        .wrap{
            .row{
                .title{
                    h4{
                        font-size: 35px;
                    }
                }
                .soc_icons_contact{
                    flex-direction: column;
                    .row{
                        width: 90%;
                        justify-content: flex-start;
                    }
                }
            }
            .content{
                form{
                    .row{
                        height: auto;
                        flex-wrap: wrap;
                        >div{
                            margin-right: 0;
                            margin-bottom: 25px;
                        }
                        textarea{
                            border: 2px #86878b solid;
                            min-height: auto;
                            padding-bottom: 0;
                            min-height: 200px;
                            margin-left: 0;
                        }
                    }
                }
            }
        }
    }

    .preview_news{
        .name_news{
            height: 400px;
            .name{
                bottom: 30px;
                .title{
                    font-size: 30px;
                }
            }
        }
        .wrap{
            .text{
                margin: 20px auto;
                article{
                    font-size: 16px;
                    img{
                        width: 80%;
                    }
                }
            }
        }
    }

    .all_news{
        margin: 120px 0 50px;
        .wrap{
            .title{
                h4{
                    font-size: 28px;
                }
                .line{
                    margin-top: 8px;
                    width: 80px;
                }
            }
            >.row{
                flex-direction: column;
                height: auto;
                align-items: center;
                .description{
                    display: flex;
                    flex-direction: column;
                    margin-top: 10px;
                    padding: 0 5px;
                    .text{
                        font-size: 14px;
                        margin-top: 10px;
                    }
                    .link{
                        align-self: center;
                        font-size: 15px;
                        padding: 5px 40px;
                        margin: 15px auto 20px;
                    }
                }
            }
        }
    }


    footer{
        .wrap{
            .other{
                .facebook,.istagram{
                    display: none;
                }
                .language{
                    width: 100%;
                }
            }
        }
    }

}

