
//prelolader
    .prelolader{
        align-items: center;
        background: #fff;
        display: flex;
        height: 100%;
        justify-content: center;
        margin: 0;
        position: fixed;
        width: 100%;
        z-index: 99999;
        .filter{
            position: absolute;
            visibility: hidden;
        }
        .dots {
            filter: url(#gooeyness);
            padding: 30px;
            .dot {
                background:@color_1;
                border-radius: 50%;
                display: inline-block;
                margin-right: 20px;
                width: 32px;
                height: 32px;
                &:first-child {
                    animation: FirstDot 3s infinite;
                }
                
                &:nth-child(2) {
                    animation: SecondDot 3s infinite;
                }
                
                &:nth-child(3) {
                    animation: ThirdDot 3s infinite;
                }
                
                &:nth-child(4) {
                    animation: FourthDot 3s infinite;
                }
                
                &:nth-child(5) {
                    animation: FifthDot 3s infinite;
                }
            }                  
        }
    }

    @keyframes FirstDot {
    0% {
        transform: scale(1) translateX(0);
    }
    25% {
        transform: scale(2.5) translateX(0);
    }
    50% {
        transform: scale(1) translateX(0);
    }
    83% {
        transform: scale(1) translateX(240px);
    }
    100% {
        transform: scale(1) translateX(0);
    }
    }
      
    @keyframes SecondDot {
    0% {
        transform: translateX(0px);
    }
    27% {
        transform: translateX(-40px);
    }
    50% {
        transform: translateX(0px);
    }
    81% {
        transform: translateX(180px);
    }
    100% {
        transform: translateX(0);
    }
    }
    
    @keyframes ThirdDot {
    0% {
        transform: translateX(0px);
    }
    29% {
        transform: translateX(-100px);
    }
    50% {
        transform: translateX(0px);
    }
    79% {
        transform: translateX(120px);
    }
    100% {
        transform: translateX(0);
    }
    }
    
    @keyframes FourthDot {
    0% {
        transform: translateX(0px);
    }
    31% {
        transform: translateX(-160px);
    }
    50% {
        transform: translateX(0px);
    }
    77% {
        transform: translateX(60px);
    }
    100% {
        transform: translateX(0);
    }
    }
    
    @keyframes FifthDot {
    0% {
        transform: scale(1) translateX(0);
    }
    33% {
        transform: scale(1) translateX(-220px);
    }
    50% {
        transform: scale(1) translateX(0);
    }
    75% {
        transform: scale(2.5) translateX(0);
    }
    100% {
        transform: scale(1) translateX(0);
    }
    }
      
//prelolader


//top_menu
    #top_menu{
        width: 100%;
        top: 0;
        left: 0;
        background: rgba(255, 255, 255,0);
        z-index: 999;
        padding: 10px 0;
        position: fixed;
        .trans(0.25s);
        color: @color;
        &.active{
            background: rgba(255, 255, 255,1);
            box-shadow: rgba(0, 0, 0, 0.5) 0px 1px 3px;
            .wrap{
                .row{
                    .logo{
                        a{
                            color: #444444;
                        }
                    }
                    .elements_top_menu{
                        nav{
                            ul{
                                li{
                                    a{
                                        color: #444444;
                                    }
                                    &:hover{
                                        &::after{
                                            opacity: 1;
                                            background: @color;
                                        }
                                        a{
                                            color: #444444;
                                        }
                                    }
                                }
                            }
                        }
                        .social_network{
                            .number{
                                color: #444444;
                            }
                        }
                    }
                }
            }
        }
        .wrap{
            .row{
                .logo{
                    a{
                        text-decoration: none;
                        justify-content: flex-start;
                        color: #fff;
                        img{
                            margin-right: 10px;
                            width: 35px;
                        }
                        .description_logo{
                            h1{
                                text-transform: uppercase;
                                font-size: 16px;
                                font-weight: 400;
                                width: auto;
                            }
                        }
                    }   
                }
                .elements_top_menu{
                    nav{
                        margin-right: 25px;
                        ul{
                            display: flex;
                            align-items: center;
                            justify-content: center; 
                            li{
                                list-style: none;
                                margin: 0 20px;
                                position: relative;
                                .trans();
                                &:first-child{
                                    margin-left: 0;
                                }
                                &:last-child{
                                    margin-right: 0;
                                }
                                &::after{
                                    content: '';
                                    width: 100%;
                                    position: absolute;
                                    left: 0;
                                    bottom: 0;
                                    margin-bottom: -5px;
                                    height: 1px;
                                    opacity: 0;
                                    background: #fff;
                                    .trans();
                                }
                                &:hover{
                                    &::after{
                                        opacity: 1;
                                    }
                                    a{
                                        color: #fff;
                                    }
                                }
                                a{
                                    font-size: 15px;
                                    .trans();
                                    text-transform: uppercase;
                                    font-weight: 600;
                                    color: #fff;
                                    text-decoration: none;
                                }
                            }
                        }
                    }
                    .social_network{
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-content: flex-end;
                        .row{
                            order: 2;
                            justify-content: flex-end;
                        }
                        .number{
                            order: 1;
                            font-size: 18px;
                            font-weight: 400;
                            color: #fdfbfb;
                        }
                        a{
                            width: 40px;
                            display: block;
                            margin: 0 5px;
                            &:last-child{
                                margin-right: 0;
                            }
                            &:first-child{
                                margin-left: 20px;
                            }
                            img{
                                width: 100%;
                                height: auto;
                            }
                        }
                        .lang{
                            order: 3;
                            margin-left: 20px;
                            display: flex;
                            position: relative;
                            flex-direction: column;
                            align-items: flex-end;
                            >img{
                                cursor: pointer;
                                height: 50px;
                            }
                            .drop{
                                border-radius: 5px;
                                background: rgba(255, 255, 255,1);
                                padding: 0 15px;
                                position: absolute;
                                display: none;
                                bottom: 0;
                                transform: translateY(100%);
                                border: 1px solid rgba(0, 0, 0, 0.5); /* Добавляем треугольник */
                                &::after{
                                    content: ''; 
                                    position: absolute; /* Абсолютное позиционирование */
                                    right: 15px; 
                                    top: -20px; /* Положение треугольника */
                                    border: 10px solid transparent; /* Прозрачные границы */
                                    border-bottom: 10px solid rgba(0, 0, 0, 0.5) /* Добавляем треугольник */
                                }
                                &::before{
                                    content: ''; 
                                    position: absolute; /* Абсолютное позиционирование */
                                    right: 15px; 
                                    z-index: 5;
                                    top: -19px; /* Положение треугольника */
                                    border: 10px solid transparent; /* Прозрачные границы */
                                    border-bottom: 10px solid rgba(255, 255, 255,1); /* Добавляем треугольник */
                                }
                                a{
                                    color: #222;
                                    font-size: 18px;
                                    text-decoration: none;
                                    margin: 10px 0;
                                    border-radius: 5px;
                                    border: 1px @color solid;
                                    padding: 5px 15px;
                                    display: flex;
                                    align-items: center;
                                    justify-content: space-between;
                                    width: 85%;
                                    .trans();
                                    cursor: pointer;
                                    &:hover,&.active{
                                        background: @color;
                                        color: #fff;
                                    }
                                    img{
                                        width: auto;
                                        margin-right: 20px;
                                        height: 45px;
                                    }
                                }
                            }
                        }
                    }
                }
                .toggle_btn{
                    display: none;
                }
            }
        }
    }
//end-top_menu

//header
    header{
        padding-top: 80px;
        min-height: 700px;
        background: url(../img/header.jpg) 50% 50% / cover no-repeat;
        position: relative;
        z-index: 0;
        &::after{
            z-index: -1;
            content: '';
            width: 100%;
            height: 200px;
            bottom: -2px;
            left: 0;
            position: absolute;
            background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%,rgba(255, 255, 255, 1) 100%);
        }
        &::before{
            z-index: -1;
            content: '';
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            position: absolute;
            background: rgba(0, 0, 0,.4);
        }
        .wrap{
            text-align: left;
            h2{
                padding: 50px 0 0;
                color: #fff;
                font-weight: 600;
                text-transform: capitalize;
                font-size: 50px;
            }
            .row{
                margin-top: 70px;
                justify-content: flex-start;
                button{
                    border: none;
                    padding: 18px 33px;
                    font-size: 16px;    
                    margin: 0 10px;
                    cursor: pointer;
                    border-radius: 5px;
                    text-transform: uppercase;
                    font-weight: 600;
                    .trans();
                    box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 10px 0px;
                    &:hover{
                        box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.3)
                    }
                    &:last-child{
                        margin-right: 0;
                    }
                    &:first-child{
                        margin-left: 0;
                    }
                }
                .consultation{
                    background: #FF6863;
                    color: #fff;
                    border: 2px #FF6863 solid;
                    &:hover{
                        background: #fff;
                        border-color: #FF6863;
                        color: #FF6863;    
                    }
                }
                .detailed{
                    background: none;
                    color: #fff;
                    border: 2px #fff solid;
                    &:hover{
                        border-color: #FF6863;
                        background: #FF6863;
                        color: #fff;    
                    }
                }
            }
            .don_btn{
                margin: 150px auto 0;
                width: 50px;
                cursor: pointer;
                animation: movement 1.7s infinite ease;
                img{
                    width: 100%;
                    height: auto;
                }
            }
        }
    }
//end-header

