                    .row{
                        align-items: center;
                        justify-content: flex-start;
                        margin: 25px auto;
                        img{
                            width: 50px;
                            height: 50px;
                            margin-right: 30px;
                        }
                        a{
                            text-decoration: none;
                            position: relative;
                            padding-bottom: 2px; 
                            font-size: 20px;
                            overflow: hidden;
                            color: @color;
                            &::after{
                                content: '';
                                width: 100%;
                                height: 2px;
                                position: absolute;
                                bottom: 0;
                                background: @color_1;
                                left: 100%;
                                .trans(.2s);
                            }
                        }
                        &:hover{
                            a{
                                &::after{
                                    left: 0;
                                }
                            }
                        }
                    }
                }
            }
            .content{
                width: 100%;
                margin: 50px auto;
                form{
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    width: 100%;
                    .row{
                        position: relative;
                        width: 100%;
                        height: 220px;
                        >div{
                            height: 100%;
                            width: 100%;
                            display: flex;
                            justify-content: space-between;
                            flex-direction: column;
                            margin-right: 30px;
                            input{
                                font-size: 16px;
                                color: #86878b;
                                padding: 10px 15px; 
                                margin: 15px 0;
                                border: none;
                                border-bottom: 2px #86878b solid;
                                &:focus{
                                    border-bottom-color: @color_1;
                                }
                                &:first-child{
                                    margin-top: 0;
                                } 
                                &:last-of-type{
                                    margin-bottom: 0;
                                }
                            }
                        }
                        textarea{
                            font-size: 16px;
                            color: #86878b;                   
                            width: 100%;
                            height: 100%;
                            padding: 10px 15px; 
                            box-sizing: border-box;
                            border: none;    
                            resize: none; /* Запрещаем изменять размер */
                            margin-left: 30px;
                            border-bottom: 2px #86878b solid; 
                            &:focus{
                                border-color: @color_1;
                            }
                        }
                    }
                    .btn_send{
                        background: none;
                        color: @color;
                        border: 2px @color solid;
                        padding: 10px 30px;
                        font-size: 22px;    
                        line-height: 1.2;
                        text-align: left;
                        text-transform: uppercase;
                        margin: 45px 0 0;
                        cursor: pointer;
                        .trans();
                        &:hover{
                            border-color: rgba(0, 0, 0, 0);
                            background: @color_1;
                            color: rgb(255, 255, 255);
                        }
                    }
                }
            }
        }
    }
//end-contact_us

